@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.homeWrapper {
    display: flex;
    align-items: center;
    max-width: 90%;
    margin: 98.9px auto 0;
    justify-content: space-between;
    font-family: 'Roboto', sans-serif;
}

.left_block {
    width: 49%;
    min-width: 457px;
}

.greeting {
    margin-top: 3px;
    font-weight: bold;
    font-size: 70px;
}

.aboutMe {
    font-size: 33.9px;
    color: #868E96;
    margin-top: 50px;
    display: flex;
}

.socImagesWrapper {
    display: flex;
}

.socImagesWrapper img {
    height: 43px;
    margin: 39.9px 13px 0px 0px;
}

.buttons {
    margin: 50px 0px 0px 0px;
}

.button {
    color: white;
    background: red;
    border: none;
    border-radius: 5px;
    padding: 13px 22px;
    font-size: 16px;
}

.resume {
    margin-left: 50px;
}

.button:hover {
    font-size: 17px;
}

.myPhoto {
    margin-top: 30px;
    width: 49%;
}

@media screen and (max-width: 1380px) {
    .greeting {
        font-size: 40px;
    }

    .aboutMe {
        font-size: 21px;
    }
}

@media screen and (max-width: 770px) {
    .homeWrapper {
        flex-direction: column;
    }

    .left_block {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-width: unset;
        width: unset;
        text-align: center;
    }

    .buttons {
        display: flex;
        width: 100%;
        justify-content: space-around;
    }

    button {
        height: 18px important;
    }

    .myPhoto {
        width: 100%;
    }
}