.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 5px 0px 5px;
}

.logo {
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-size: 30px;
    color: red;
    margin-left: 1%;
}

.menuItems, .darkThemeActiveNavItems {
    display: flex;
    flex-wrap: wrap;
}

.menuItems a, .darkThemeActiveNavItems a {
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-size: 19px;
    padding: 15px 20px;
}

.menuItems a {
    color: black;
}

.themeToggle, .darkModeActiveToggle {
    position: relative;
    padding: 20px;
}

.darkThemeActiveNavItems a {
    color: white;
}

.darkThemeActiveNavItems a:hover .darkModeActiveToggle {
    background-color: #55198b;
}

.menuItems a:hover .themeToggle{
    background: #f0f2f3;
}

.toggleControl {
    display: none;
}

label {
    background-color: red;
    border-radius: 60px;
    cursor: pointer;
    outline: none;
    user-select: none;
}

label::before, label::after {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 50px;
    height: 26px;
    content: '';
    transition: all 0.4s;
}

label::before {
    background-color: #CCCCCC;
    border-radius: 60px;
}

label::after {
    width: 28px;
    background-color: white;
    border-radius: 50%;
}

.toggleControl:checked+label::before {
    background-color: #2196f3;
}

.toggleControl:checked+label::after {
    margin-left: 22.5px;
}

.hamburgerMenu {
    display: none;
}

@media only screen and (max-width: 765px) {
    .navbar {
        position: sticky;
        z-index: 999;
        padding: 15px 0px 0px 0px;
    }

    .menuItems {
        display: none;
    }

    .hamburgerActiveNavItems {
        display: flex;
        position: absolute;
        margin-top: 450.2px;
        width: 100%;
        background-color: white;
        flex-direction: column;
    }

    .darkThemeActiveNavItems {
        background-color: #171C28;
    }

    .hamburgerMenu {
        display: block;
        width: 30px;
    }
}

@media only screen and (max-width: 520px) {
    .logo {
        text-decoration: none;
        font-family: 'Roboto', sans-serif;
        font-size: 30px;
        color: red;
        margin-left: 1%;
    }

}