.body {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 110px 0px 100px 0px;
    width: 100%;
}

.Tigran_img {
    height: 550px;
    margin-left: 18.5%;
    display: flex;
}

.whatIDo {
    display: flex;
    flex-direction: column;
    margin-left: 20%;
}

.header {
    font-size: 39px;
    display: flex;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 29px;
}

.aboutMeBlock {
    display: flex;
}

.about_me {
    font-size: 23.5px;
    color: #868E96;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 30px;
    width: 750px;
}

.languages {
    display: flex;
    flex-wrap: wrap;
}

.mySkillsWrapper {
    margin-top: 50px;
    width: 700px;
}

.mySkillsWrapper span {
    display: flex;
    font-size: 23px;
    margin-top: 20px;
    color: #868E96;
    font-family: 'Roboto', sans-serif;
}

@media screen and (max-width: 520px) {
    .body {
        width: 520px;
        margin-top: 150px;
        flex-direction: column-reverse;
    }

    .skillsImage {
        display: flex;
        justify-content: center;
    }

    .Tigran_img {
        height: 350px;
        width: 300px;
        margin: 39px 0px 0px 0px;
    }

    .whatIDo {
        margin: 0px;
        width: 520px;
        justify-content: center;
    }

    .header {
        font-size: 27px;
        display: flex;
        justify-content: center;
    }

    .languagesWrapper {
        display: flex;
        justify-content: center;
        width: 520px;
    }

    .languages {
        justify-content: center;
        width: 428px;
    }

    .aboutMeBlock {
        display: flex;
        justify-content: center;
        width: 520px;
    }

    .about_me {
        display: flex;
        font-size: 17px;
        margin-bottom: 20px;
        width: 428px;
        text-align: center;
    }

    .mySkillsWrapper {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 30px;
        width: 520px;
    }

    .mySkillsWrapper span {
        justify-content: center;
        font-size: 17px;
        margin-top: 10px;
        width: 428px;
    }
}

