/* styles of project's Cart */

.body {
    display: flex;
}

a {
    color: black;
    text-decoration: none;
}

.projectCart {
    width: 440px;
    height: 200px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    margin: 0px 27px 20px 0px;
}

.projectCart:hover {
    box-shadow: 0 10px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transition: 0.3s;
}

.project_name {
    margin: 29px 30px 0px 0px;
    font-size: 25px;
}

.about_project {
    margin: 23px 30px 20px 0px;
    display: flex;
    font-size: 18px;
    color: #868E96;
}

.project_cart_body {
    padding-left: 30px;
}

.kb {
    margin-left: 60%;
}

.bottom_div {
    display: flex;
    font-size: 13px;
    color: #868E96;
}

@media screen and (max-width: 520px) {
    .projectCart {
        width: 305px;
        height: 175px;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        display: flex;
        padding: 32px;
        margin: 0px 0px 0px 0px;
    }

    .project_name {
        margin: 0px;
        font-size: 23.7px;
    }
}